export class IVPAIDElement {
  constructor(data, element, vpaidSettings) {
    this.data = data;
    this.element = element;
    this.vpaidSettings = vpaidSettings;
    this.createElement();
  }

  // Returns the HTML Element
  createElement() {
    throw new Error("createElement() must be implemented.");
  }

  // Binds GSAP animations
  addToTimeline(adTimeline) {
    const self = this;
    adTimeline.to(self.domElement, {
      display: "block",  // Element appears in layout
      opacity: 1,
      duration: 0.1,
      delay: self.element.start, // Element appears at the correct time
    });

    adTimeline.to(self.domElement, {
      display: "none",  // Element disappears from layout
      opacity: 0,
      duration: 0.1,
      delay: self.element.start + self.element.duration, // Element appears at the correct time
    });
  }

  // If an element requires external dependencies
  loadDependencies(callback) {
    if (callback) callback();
  }

  needsPauseResume() {
    return false;
  }

  needsVolume() {
    return false;
  }

  pause() { }

  resume() { }

  setVolume(volume) { }

  needsSeek() {
    return false;
  }

  seek(newTime) { }

  getRootElement() {
    return this.domElement;
  }

  createRootElement() {
    this.domElement = document.createElement("div");
    this.domElement.style = {};
    this.domElement.style.position = "absolute";

    // Compute width and height
    const computedWidth = (this.element.width / this.data.outputResolution.width) * this.vpaidSettings.adWidth;
    const computedHeight = (this.element.height / this.data.outputResolution.height) * this.vpaidSettings.adHeight;

    // Compute left and top positions
    const computedLeft = (this.element.x / this.data.outputResolution.width) * this.vpaidSettings.adWidth;
    const computedTop = (this.element.y / this.data.outputResolution.height) * this.vpaidSettings.adHeight;

    // Assign computed styles
    this.domElement.style.width = computedWidth + "px";
    this.domElement.style.height = computedHeight + "px";
    this.domElement.style.left = computedLeft + "px";
    this.domElement.style.top = computedTop + "px";

    // Log output for debugging
    console.log(`Element ${this.element.id} and ${this.element.type}  Computed Styles:`);
    console.log(`Width: ${computedWidth}px, Height: ${computedHeight}px`);
    console.log(`Left: ${computedLeft}px, Top: ${computedTop}px`);
    console.log(`Original Dimensions: Width: ${this.element.width}, Height: ${this.element.height}`);
    console.log(`Original Position: X: ${this.element.x}, Y: ${this.element.y}`);
    console.log(`Output Resolution: ${this.data.outputResolution.width}x${this.data.outputResolution.height}`);
    console.log(`AdContainer Size: ${this.vpaidSettings.adWidth}x${this.vpaidSettings.adHeight}`);

  }

  // Call this when the element has fully loaded its wresources
  markLoaded() {
    if (this.vpaidSettings.vpaidInstance) {
      this.vpaidSettings.vpaidInstance.markElementLoaded(this, this.element.id);
    }
  }
}
